.dj-header {
  padding: 0 26px;
  border-bottom: 1px solid #e1e1e1;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  z-index: 99;
  width: 100vw;
}
.dj-header .dj-logo {
  height: 48px;
  line-height: 48px;
  -webkit-flex: 1 1;
          flex: 1 1;
}
.dj-header .dj-logo img {
  max-height: 48px;
}
.main-contaniner {
  margin-top: 56px;
}
.main-contaniner .main-sidder {
  height: calc(100vh - 56px);
}
.main-contaniner .main-content {
  height: calc(100vh - 56px);
  overflow-y: auto;
  background-color: #f9faff;
}
